<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-textarea rows="1" label="Course Name" v-model="inputName" outlined>
        </v-textarea>
      </v-col>
      <v-col cols="2">
        <v-btn color="accent" @click="create">New Course</v-btn>
      </v-col>
    </v-row>
    <v-card outlined raised>
      <v-data-table :headers="headers" :items="courses" class="elevation-1">
        <template v-slot:item.action="{ item }">
          <v-icon @click="deleteItem(item)" color="error">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import CourseService from "../services/CourseService";
export default {
  data() {
    return {
      courses: [],
      headers: [
        {
          text: "Name",
          align: "start",
          value: "name"
        },
        { text: "Group 1", value: "group1" },
        { text: "Group 2", value: "group2" },
        { text: "Group 3", value: "group3" },
        { text: "Actions", value: "action", sortable: false }
      ],
      inputName: ""
    };
  },
  created() {
    CourseService.GetCourses().then(courses => {
      this.courses = courses;
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.courses.indexOf(item);
      const isConfirmed = confirm("Are you sure you want to delete this item?");
      if (isConfirmed) {
        CourseService.DeleteCourse(item.id).then(() => {
          this.courses.splice(index, 1);
        });
      }
    },
    create() {
      CourseService.CreateCourse({
        name: this.inputName
      }).then(() => {
        this.$router.go();
      });
    }
  }
};
</script>

